@import "https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

ul, li {
  list-style: none;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

* {
  box-sizing: border-box;
  outline: none;
}

html {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}

img {
  max-width: 100%;
}

:root {
  --body-font: "Inter", sans-serif;
  --theme-bg: #1f1d2b;
  --body-color: #808191;
  --button-bg: #353340;
  --border-color: rgba(128, 129, 145, .24);
  --video-bg: #252936;
  --delay: 0s;
}

.container {
  background-color: var(--theme-bg);
  width: 1240px;
  height: 95vh;
  width: 100%;
  border-radius: 20px;
  font-size: 15px;
  font-weight: 500;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 20px 50px rgba(0, 0, 0, .3);
}

.sidebar {
  width: 220px;
  height: 100%;
  flex-direction: column;
  flex-shrink: 0;
  padding: 30px;
  transition-duration: .2s;
  display: flex;
  overflow-x: hidden;
  overflow-y: auto;
}

.logo {
  width: 30px;
  height: 30px;
  color: #fff;
  background-color: #22b07d;
  border-radius: 50%;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  display: none;
}

.logo-expand {
  color: #fff;
  font-size: 19px;
  font-weight: 600;
  line-height: 34px;
  text-decoration: none;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.logo-expand:before {
  content: "";
  background: var(--theme-bg);
  width: 200px;
  height: 70px;
  z-index: -1;
  position: absolute;
  top: -30px;
  left: 0;
}

.sidebar-link:hover, .sidebar-link.is-active {
  color: #fff;
  font-weight: 600;
}

.sidebar-link:hover:nth-child(2n+1) svg, .sidebar-link.is-active:nth-child(2n+1) svg {
  background: #ff7551;
}

.sidebar-link:hover:nth-child(2n) svg, .sidebar-link.is-active:nth-child(2n) svg {
  background: #32a7e2;
}

.sidebar-link:hover:nth-child(2n+3) svg, .sidebar-link.is-active:nth-child(2n+3) svg {
  background: #6c5ecf;
}

.sidebar.collapse {
  width: 90px;
  border-right: 1px solid var(--border-color);
}

.sidebar.collapse .logo-expand, .sidebar.collapse .side-title {
  display: none;
}

.sidebar.collapse .logo {
  display: flex;
}

.sidebar.collapse .side-wrapper {
  width: 30px;
}

.sidebar.collapse .side-menu svg {
  margin-right: 30px;
}

@keyframes bottom {
  0% {
    opacity: 0;
    transform: translateY(100px);
  }

  100% {
    opacity: 1;
    transform: none;
  }
}

.side-menu {
  flex-direction: column;
  display: flex;
}

.side-menu a {
  color: var(--body-color);
  align-items: center;
  margin-top: 26px;
  text-decoration: none;
  display: flex;
}

.side-menu svg {
  width: 30px;
  background-color: var(--button-bg);
  border-radius: 10px;
  flex-shrink: 0;
  margin-right: 16px;
  padding: 8px;
}

.side-menu svg:hover {
  color: #fff;
}

.side-title {
  letter-spacing: .07em;
  margin-bottom: 24px;
  font-size: 12px;
}

.side-wrapper {
  border-bottom: 1px solid var(--border-color);
  width: 145px;
  padding: 36px 0;
}

.side-wrapper + .side-wrapper {
  border-bottom: none;
}

.wrapper {
  flex-direction: column;
  flex-grow: 1;
  display: flex;
}

.header {
  flex-shrink: 0;
  align-items: center;
  padding: 30px;
  display: flex;
}

.search-bar {
  height: 34px;
  width: 100%;
  max-width: 450px;
  display: flex;
}

.search-bar input {
  width: 100%;
  height: 100%;
  background-color: var(--button-bg);
  font-family: var(--body-font);
  color: #fff;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 56.966 56.966' fill='%23717790c7'%3e%3cpath d='M55.146 51.887L41.588 37.786A22.926 22.926 0 0046.984 23c0-12.682-10.318-23-23-23s-23 10.318-23 23 10.318 23 23 23c4.761 0 9.298-1.436 13.177-4.162l13.661 14.208c.571.593 1.339.92 2.162.92.779 0 1.518-.297 2.079-.837a3.004 3.004 0 00.083-4.242zM23.984 6c9.374 0 17 7.626 17 17s-7.626 17-17 17-17-7.626-17-17 7.626-17 17-17z'/%3e%3c/svg%3e");
  background-position: 96%;
  background-repeat: no-repeat;
  background-size: 14px;
  border: none;
  border-radius: 8px;
  padding: 0 40px 0 16px;
  font-size: 14px;
  font-weight: 500;
  box-shadow: 0 0 0 2px rgba(134, 140, 160, .02);
}

.user-settings {
  flex-shrink: 0;
  align-items: center;
  margin-left: auto;
  padding-left: 20px;
  display: flex;
}

.user-settings svg {
  width: 10px;
  flex-shrink: 0;
}

@media screen and (max-width: 575px) {
  .user-settings svg {
    display: none;
  }
}

.notify {
  position: relative;
}

.notify svg {
  width: 20px;
  flex-shrink: 0;
  margin-left: 24px;
}

.notification {
  width: 8px;
  height: 8px;
  border: 1px solid var(--theme-bg);
  background-color: #ec5252;
  border-radius: 50%;
  position: absolute;
  top: -2px;
  right: 1px;
}

@media screen and (max-width: 575px) {
  .notification {
    display: none;
  }
}

.user-img {
  width: 30px;
  height: 30px;
  object-fit: cover;
  border-radius: 50%;
  flex-shrink: 0;
}

.user-name {
  color: #fff;
  margin: 0 6px 0 12px;
  font-size: 14px;
}

@media screen and (max-width: 575px) {
  .user-name {
    display: none;
  }
}

.main-container {
  flex-direction: column;
  flex-grow: 1;
  padding: 0 30px 30px;
  display: flex;
  overflow: auto;
}

.anim {
  animation: bottom .8s var(--delay) both;
}

.main-header {
  color: #fff;
  z-index: 11;
  background: linear-gradient(#1f1d2b 0%, #1f1d2b 78%, rgba(31, 29, 43, 0) 100%);
  padding-bottom: 20px;
  font-size: 30px;
  font-weight: 700;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
}

.small-header {
  color: #fff;
  margin: 30px 0 20px;
  font-size: 24px;
  font-weight: 500;
}

.main-blogs {
  align-items: center;
  display: flex;
}

.main-blog__author {
  align-items: center;
  padding-bottom: 10px;
  display: flex;
}

.main-blog__author.tips {
  flex-direction: column-reverse;
  align-items: flex-start;
}

.main-blog__title {
  max-width: 12ch;
  letter-spacing: 1px;
  color: #fff;
  margin-bottom: 30px;
  font-size: 25px;
  font-weight: 600;
}

.main-blog {
  width: 65%;
  background-color: #31abbd;
  background-image: url("https://assets.codepen.io/3364143/skate-removebg-preview.png");
  background-position-x: 150px;
  background-repeat: no-repeat;
  background-size: 80%;
  border-radius: 20px;
  flex-direction: column;
  align-self: stretch;
  padding: 30px;
  transition: background .3s;
  display: flex;
  position: relative;
  overflow: hidden;
}

.main-blog + .main-blog {
  width: 35%;
  background-image: url("https://c0.anyrgb.com/images/1020/945/venice-beach-2018-outdoors-sport-men-jumping-desert-sunset-extreme-sports-one-person-action.jpg");
  background-color: unset;
  filter: saturate(1.4);
  background-position-x: 0;
  background-size: 139%;
  margin-left: 20px;
}

.main-blog + .main-blog .author-img {
  border-color: rgba(255, 255, 255, .75);
  margin-top: 14px;
}

.main-blog + .main-blog .author-img__wrapper svg {
  color: #e7bb7d;
  border-color: #ffe6b2;
}

.main-blog + .main-blog .author-detail {
  margin-left: 0;
}

@media screen and (max-width: 905px) {
  .main-blog, .main-blog + .main-blog {
    width: 50%;
    padding: 30px;
  }

  .main-blog {
    background-blend-mode: overlay;
    background-position-x: center;
    background-size: cover;
  }
}

.main-blog__time {
  color: #fff;
  background: rgba(21, 13, 13, .44);
  border-radius: 6px;
  padding: 3px 8px;
  font-size: 12px;
  position: absolute;
  bottom: 20px;
  right: 20px;
}

.author-img {
  width: 52px;
  height: 52px;
  object-fit: cover;
  border: 1px solid rgba(255, 255, 255, .75);
  border-radius: 50%;
  padding: 4px;
}

.author-img__wrapper {
  flex-shrink: 0;
  position: relative;
}

.author-img__wrapper svg {
  width: 16px;
  color: #0daabc;
  background-color: #fff;
  border: 2px solid #0daabc;
  border-radius: 50%;
  padding: 2px;
  position: absolute;
  bottom: 5px;
  right: 0;
}

.author-name {
  color: #fff;
  margin-bottom: 8px;
  font-size: 15px;
  font-weight: 500;
}

.author-info {
  color: #fff;
  font-size: 13px;
  font-weight: 400;
}

.author-detail {
  margin-left: 16px;
}

.seperate {
  width: 3px;
  height: 3px;
  vertical-align: middle;
  background-color: #fff;
  border-radius: 50%;
  margin: 0 6px;
  display: inline-block;
}

.seperate.video-seperate {
  background-color: var(--body-color);
}

.videos {
  width: 100%;
  grid-column-gap: 20px;
  grid-row-gap: 20px;
  grid-template-columns: repeat(4, 1fr);
  display: grid;
}

@media screen and (max-width: 980px) {
  .videos {
    grid-template-columns: repeat(2, 1fr);
  }
}

.video {
  background-color: var(--video-bg);
  border-radius: 20px;
  transition: all .4s;
  position: relative;
  overflow: hidden;
}

.video-wrapper {
  position: relative;
}

.video-name {
  color: #fff;
  background-color: var(--video-bg);
  z-index: 9;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  padding: 12px 20px 0;
  font-size: 16px;
  line-height: 1.4em;
  display: -webkit-box;
  position: relative;
  overflow: hidden;
}

.video-view {
  background-color: var(--video-bg);
  padding: 12px 20px 20px;
  font-size: 12px;
  position: relative;
}

.video-by {
  padding: 20px 20px 0;
  transition: all .3s;
  display: inline-flex;
  position: relative;
}

.video-by:before {
  content: "";
  width: 6px;
  height: 6px;
  background-color: #22b07d;
  border-radius: 50%;
  position: absolute;
  top: 26px;
  right: 5px;
}

.video-by.offline:before {
  background-color: #ff7551;
}

.video-time {
  color: rgba(255, 255, 255, .85);
  z-index: 1;
  background: rgba(21, 13, 13, .44);
  border-radius: 6px;
  padding: 3px 8px;
  font-size: 12px;
  position: absolute;
  top: 10px;
  right: 8px;
}

.video:hover video {
  transform-origin: center;
  transform: scale(1.6);
}

.video:hover .video-time {
  display: none;
}

.video:hover .video-author {
  z-index: 10;
  bottom: -65px;
  right: -3px;
  transform: scale(.6);
}

.video:hover .video-by {
  opacity: 0;
}

.video-author {
  transition: all .4s;
  position: absolute;
  bottom: -25px;
  right: 10px;
}

.video-author svg {
  color: #fff;
  border-color: var(--video-bg);
  background-color: #0aa0f7;
}

video {
  max-width: 100%;
  width: 100%;
  cursor: pointer;
  border-radius: 20px 20px 0 0;
  transition: all .4s;
  display: block;
}

.stream-area {
  display: none;
}

@media screen and (max-width: 940px) {
  .stream-area {
    flex-direction: column;
  }

  .stream-area .video-stream {
    width: 100%;
  }

  .stream-area .chat-stream {
    margin-top: 30px;
    margin-left: 0;
  }

  .stream-area .video-js.vjs-fluid {
    min-height: 250px;
  }

  .stream-area .msg__content {
    max-width: 100%;
  }
}

.show .stream-area {
  display: flex;
}

.show .main-header, .show .main-blogs, .show .small-header, .show .videos {
  display: none;
}

.video-stream {
  width: 65%;
  object-fit: cover;
  transition: all .3s;
}

.video-stream:hover .video-js .vjs-big-play-button {
  opacity: 1;
}

.video-p {
  object-fit: cover;
  border-radius: 50%;
  flex-shrink: 0;
  margin-right: 12px;
  position: relative;
  top: 0;
  left: 0;
}

.video-p .author-img {
  border: 0;
}

.video-p .author-img-wrapper {
  align-items: center;
  display: flex;
}

.video-p .author-img-wrapper .author-img {
  border: 0;
}

.video-p .author-img-wrapper svg {
  width: 20px;
  padding: 4px;
}

@media screen and (max-width: 650px) {
  .video-p .author-img-wrapper {
    flex-direction: column;
  }

  .video-p .author-img-wrapper .button-wrapper {
    margin: 20px auto 0;
  }

  .video-p .author-img-wrapper .video-p-detail {
    flex-direction: column;
    align-items: center;
    display: flex;
  }

  .video-p .author-img-wrapper .video-p {
    margin-right: 0;
  }
}

.video-p .author-img-wrapper-sub {
  font-size: 12px;
}

.video-p .author-img-wrapper-title {
  color: #fff;
  margin: 16px 0 20px;
  font-size: 24px;
  line-height: 1.4em;
}

.video-p .author-img-wrapper-subtitle {
  max-width: 60ch;
  font-size: 14px;
  line-height: 1.5em;
}

.video-p .author-img-wrapper-subtitle + .author-img-wrapper-subtitle {
  margin-top: 16px;
}

.author-img-wrapper-name {
  color: #fff;
  align-items: center;
  margin-bottom: 8px;
  display: flex;
}

.author-img-wrapper-name:after {
  content: "";
  width: 6px;
  height: 6px;
  background-color: #22b07d;
  border-radius: 50%;
  margin-left: 8px;
  display: inline-block;
}

.author-img-wrapper-name.offline:after {
  background-color: #ff7551;
}

.video-content {
  width: 100%;
}

.button-wrapper {
  align-items: center;
  margin-left: auto;
  display: flex;
}

.like {
  background-color: var(--button-bg);
  color: #fff;
  font-family: var(--body-font);
  cursor: pointer;
  border: 0;
  border-radius: 8px;
  align-items: center;
  padding: 10px 16px;
  font-size: 14px;
  display: flex;
}

.like.red {
  background-color: #ea5f5f;
}

.like svg {
  width: 18px;
  flex-shrink: 0;
  margin-right: 10px;
  padding: 0;
}

.like + .like {
  margin-left: 16px;
}

.video-stats {
  margin-left: 30px;
}

.video-detail {
  width: 100%;
  margin-top: 30px;
  display: flex;
}

.chat-header {
  color: #fff;
  z-index: 1;
  border-bottom: 1px solid var(--border-color);
  background-color: #252836;
  align-items: center;
  padding: 20px 0;
  font-size: 16px;
  font-weight: 600;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
}

.chat-header svg {
  width: 15px;
  flex-shrink: 0;
  margin-right: 6px;
}

.chat-header span {
  color: var(--body-color);
  align-items: center;
  margin-left: auto;
  font-size: 12px;
  display: flex;
}

.chat-stream {
  flex-grow: 1;
  margin-left: 30px;
}

.chat {
  max-height: 414px;
  background-color: #252836;
  border-radius: 20px;
  padding: 0 20px;
  overflow: auto;
}

.chat-footer {
  width: calc(100% + 20px);
  background-color: #252836;
  align-items: center;
  padding-bottom: 12px;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
  left: 0;
}

.chat-footer input {
  width: 100%;
  color: #fff;
  font-size: 12px;
  font-weight: 500;
  font-family: var(--body-font);
  background-color: #2d303e;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='24' height='24' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M2 12C2 6.48 6.47 2 12 2c5.52 0 10 4.48 10 10s-4.48 10-10 10C6.47 22 2 17.52 2 12zm5.52 1.2c-.66 0-1.2-.54-1.2-1.2 0-.66.54-1.2 1.2-1.2.66 0 1.19.54 1.19 1.2 0 .66-.53 1.2-1.19 1.2zM10.8 12c0 .66.54 1.2 1.2 1.2.66 0 1.19-.54 1.19-1.2a1.194 1.194 0 10-2.39 0zm4.48 0a1.195 1.195 0 102.39 0 1.194 1.194 0 10-2.39 0z' fill='%236c6e78'/%3e%3c/svg%3e");
  background-position: 8px;
  background-repeat: no-repeat;
  background-size: 24px;
  border: 0;
  border-radius: 20px;
  margin-left: -10px;
  padding: 12px 40px;
}

.chat-footer input input::-ms-input-placeholder {
  color: #6c6e78;
}

.chat-footer input input::placeholder {
  color: #6c6e78;
}

.chat-footer:before {
  content: "";
  width: 18px;
  height: 18px;
  background-color: #6c5ecf;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg viewBox='0 0 24 24' fill='white' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M21.435 2.582a1.933 1.933 0 00-1.93-.503L3.408 6.759a1.92 1.92 0 00-1.384 1.522c-.142.75.355 1.704 1.003 2.102l5.033 3.094a1.304 1.304 0 001.61-.194l5.763-5.799a.734.734 0 011.06 0c.29.292.29.765 0 1.067l-5.773 5.8c-.428.43-.508 1.1-.193 1.62l3.075 5.083c.36.604.98.946 1.66.946.08 0 .17 0 .251-.01.78-.1 1.4-.634 1.63-1.39l4.773-16.075c.21-.685.02-1.43-.48-1.943z'/%3e%3c/svg%3e");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 14px;
  border-radius: 50%;
  padding: 4px;
  position: absolute;
  right: 16px;
}

.chat-footer-vid__title {
  color: #fff;
  font-size: 18px;
}

.chat-footer-vid__container {
